import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http';
import { MyserviceService } from '../myservice.service';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';

@Component({
  selector: 'app-requestdetails',
  templateUrl: './requestdetails.component.html',
  styleUrls: ['./requestdetails.component.css']
})
export class RequestdetailsComponent implements OnInit {
  
  collection = [];
  RequestID = '';
  
  constructor(private formBuilder: FormBuilder,private router: Router, private http: HttpClient,public myservice: MyserviceService,private route: ActivatedRoute) { }

  ngOnInit() {
	this.myservice.setTitle('Request Details');
    this.RequestID = this.route.snapshot.paramMap.get('requestid'); 
    console.log("requestid="+this.RequestID);
	  
	let params = {'requestid':this.RequestID};
       
	this.myservice.httpPostMethod('http://localhost:81/Angular/api_reqsumery.php', params, 'application/json', false, 'custservice')
	.then(resData => {

	if(resData != undefined){
		console.log("inside of resdata");
		if(resData.hasOwnProperty('status')){
			console.log("inside of hasOwnProperty");
			if(resData['status'] == 200){
			  console.log("status=",resData);
			  let emirates = resData['body'];
			  console.log("emirates="+emirates);//return false;
				if(emirates != undefined && emirates != null && emirates != ""){
					console.log("&&&&&&",emirates.success);
					if(emirates.success) {
						var mesg = emirates.message;
						console.log("mesg="+mesg);
						var splitcol = mesg.split('$$');
						
						this.collection.push({ "request_category": splitcol[0], "request_subcategory": splitcol[1], "request_type": splitcol[2], "request_desc": splitcol[3], "groupname": splitcol[4], "requestsource": splitcol[5], "agreement_no": splitcol[6], "request_id": splitcol[7], "customerid": splitcol[8], "customer_mailid": splitcol[9], "customer_name": splitcol[10], "requeststatus": splitcol[11], "remarks": splitcol[12], "request_classification": splitcol[13], "customer_mobileno": splitcol[14], "vehicleno": splitcol[15], "request_creation_dt": splitcol[16], "tentative_closing_dt": splitcol[17], "actual_closing_dt": splitcol[18] });
										
					} 
				}
			}else{ 
			  console.log("Get City List API - Error Occured : "+resData['txt']);
			  return false;
			}
		}
	}
	  },
	  error =>  {
		console.log("Error", error);
		//this.spinner.hide();
	  });
  }

	GoBack(){
		this.router.navigate(['/requestslist']);
	}

}


