import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http';
import { MyserviceService } from '../myservice.service';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { CookieService} from 'ngx-cookie-service';

@Component({
  selector: 'app-rcalist',
  templateUrl: './rcalist.component.html',
  styleUrls: ['./rcalist.component.css']
})
export class RcalistComponent implements OnInit {
collection = [];
  constructor(private formBuilder: FormBuilder,private router: Router, private http: HttpClient,public myservice: MyserviceService,private route: ActivatedRoute,private cookieService: CookieService) { }

  ngOnInit() {
	  this.myservice.setTitle('RCA');
	  
	  let params = {'recidval':''};
       
		this.myservice.httpPostMethod('http://localhost:81/Angular/rcalist.php', params, 'application/json', false, 'roleslist')
		.then(resData => {

		if(resData != undefined){
			console.log("inside of resdata");
			if(resData.hasOwnProperty('status')){
				console.log("inside of hasOwnProperty");
				if(resData['status'] == 200){
				  console.log("status=",resData);
				  let emirates = resData['body'];
				  console.log("emirates="+emirates);//return false;
					if(emirates != undefined && emirates != null && emirates != ""){
						console.log("&&&&&&",emirates.success);
						if(emirates.success) {
							var mesg = emirates.message;
							console.log("mesg="+mesg);
							var splitrow = mesg.split('||');
							var reclen = splitrow.length;
							for(var i=0;i<reclen-1;i++){
								var splitcol = splitrow[i].split('$$');
								this.collection.push({ "complaintid": splitcol[0], "rcastatusval": splitcol[1], "why": splitcol[2],  "how": splitcol[3],  "reasoncode": splitcol[4],  "what": splitcol[5],  "when": splitcol[6],  "remarks": splitcol[7],  "recid": splitcol[8] });
							}
							
											
						} 
					}
				}else{ 
				  console.log("Get City List API - Error Occured : "+resData['txt']);
				  return false;
				}
			}
		}
	  },
	  error =>  {
		console.log("Error", error);
		//this.spinner.hide();
	  });
	  
  }
  
  EditSelrec(recidval) {
	console.log("EditSelrec="+recidval);  
	this.router.navigate(['/rca',{'recid':recidval}]);
  }
  
  fn_redirect(requestidval) {
	this.router.navigate(['/rcadetails',{'recid':requestidval}]);  
  }
  


}
