import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http';
import { MyserviceService } from '../myservice.service';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';


@Component({
  selector: 'app-createcommtemp',
  templateUrl: './createcommtemp.component.html',
  styleUrls: ['./createcommtemp.component.css']
})
export class CreatecommtempComponent implements OnInit {

  companyrole = ['CIFCL','HFC'];	
  communsdata : FormGroup;
  submitted = false;
  //defaultval = false;
  existgroupid = '';
  collection = [];
  
  constructor(private formBuilder: FormBuilder,private router: Router, private http: HttpClient,public myservice: MyserviceService,private route: ActivatedRoute) { }
  
  
  ngOnInit() {
	this.myservice.setTitle('Communication List');
	this.existgroupid = this.route.snapshot.paramMap.get('groupid'); 
	
	this.communsdata = this.formBuilder.group({
	   tmplatename: ['', Validators.required],
	   smstmplatename: ['', ''],
	   emailtmplatename: ['', ''],
	   uploadtemplates: ['', Validators.required]
	   
	   /* extgroupname: ['', Validators.required],
	   extgroupid: ['', ''],
	   extcompany: ['', Validators.required] */
	});
	
  }

onClickExit() {
	if(this.existgroupid!=null && this.existgroupid!='') {
		this.router.navigate(['/commtempdetails',{"groupid":this.existgroupid}]);
	} else {
		this.router.navigate(['/commtempslist']);
	}
}

}
