import { BrowserModule } from '@angular/platform-browser';
import { NgModule,Input } from '@angular/core';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { HeaderComponent } from '../header/header.component';

import { MyserviceService } from '../myservice.service';
//import { DataTableDirective } from 'angular-datatables';
import { CookieService} from 'ngx-cookie-service';

@Component({
  selector: 'app-custservice',
  templateUrl: './custservice.component.html',
  styleUrls: ['./custservice.component.css']
})
export class CustserviceComponent implements OnInit {
  custservicedata: FormGroup;
  quicksrch = true;
  verifycheckbx = false;
  linkval = '';
  
  collection = [];
  
  constructor(private formBuilder: FormBuilder,private router: Router, private http: HttpClient,public myservice: MyserviceService,private cookieService: CookieService) { }
  
  ngOnInit() {
    this.myservice.setTitle('Customer Service');
	
	this.custservicedata = this.formBuilder.group({
      custname: ['', Validators.pattern(/^[a-zA-Z]+$/)],
      aadherno: ['', Validators.pattern(/^[0-9]\d{12}$/)],
      mobileno: ['', Validators.pattern(/^[6-9]\d{9}$/)],
      panno: ['', Validators.pattern(/^[[a-zA-Z0-9]\d{10}$/)],
      agreementno: ['',''],
      emailid: ['',''],
      api_chkbx: ['','']
     
    });

    
  }

  EnableQuicksrch() {
    this.quicksrch = true;
  }

  EnableAdvancesrch() {
    this.quicksrch = false;
  }

  onClickSubmit(searchvalue){

    if(this.custservicedata.invalid) {
      return false;
    }

    console.log("custservice="+searchvalue.api_chkbx);
    if(searchvalue.api_chkbx != "") {
      if(searchvalue.mobileno!='') {
        let api_name = searchvalue.api_chkbx;
        let agreement_no = searchvalue.agreementno;
        let reg_mobile = searchvalue.mobileno;
      // var params = 'usrname='+usrname+'&pwd='+pwd;x-www-form-urlencoded
        console.log("username="+api_name+"--password="+agreement_no+"--reg_mobile="+reg_mobile);
        
      let params = {'api_name':api_name,'agreement_no':agreement_no,'reg_mobile':reg_mobile};
       
          this.myservice.httpPostMethod('http://localhost:81/Angular/api_search.php', params, 'application/json', false, 'custservice')
          .then(resData => {

            if(resData != undefined){
              console.log("inside of resdata");
              if(resData.hasOwnProperty('status')){
                console.log("inside of hasOwnProperty");
                if(resData['status'] == 200){
                  console.log("status=",resData);
                  let emirates = resData['body'];
                  console.log("emirates="+emirates);//return false;
                  if(emirates != undefined && emirates != null && emirates != ""){
                    console.log("&&&&&&",emirates.success);
                    if(emirates.success) {
                      var mesg = emirates.message;
                      console.log("mesg="+mesg);
                      var splitrw = mesg.split('||');
                      console.log("splitrw="+splitrw.length);
                      var reclen = splitrw.length;
                      for(var i=0;i<reclen-1;i++) {
                        console.log("splitrw of i="+splitrw[i]);
                          var splitcol = splitrw[i].split('$$');
                          console.log("splitcol="+splitcol.length);
                          //for(var j=0;j<splitcol.length;j++) {
                            //console.log("splitcol of j="+splitcol[j]);
                            this.collection.push({ "slno": i+1, "id": splitcol[0], "agreementno": splitcol[1], "customername": splitcol[2], "dob": splitcol[3], "emailid": splitcol[4], "cifno": splitcol[5], "groupid": splitcol[6], "mobileno": splitcol[7], "panno": splitcol[8], "customerid": splitcol[9],"loanamtfin": splitcol[10], "emi": splitcol[11], "disbdate": splitcol[12], "nocdate": splitcol[13], "disbstatus": splitcol[14], "npastage": splitcol[15], "branchname": splitcol[17], "vehicleno": splitcol[18], "loanstatus": splitcol[19] });
                         // }
                      }


                      //alert("Successfully Logged-In");
                      //this.router.navigate(['/menu']);
                    } else {
                      alert("Invalid User credentilas");
                      return false;
                    }
                  }
                }else{ 
                  console.log("Get City List API - Error Occured : "+resData['txt']);
                  return false;
                }
              }
            }
          },
          error =>  {
            console.log("Error", error);
            //this.spinner.hide();
          });
        
      } else {
        alert("Please Enter Mobile Number");
        return false;
      }
    } else {
      alert("Please Select API Type");
      return false;
    }
  
  }
 
 OnClickClear(){
  this.custservicedata.reset();
 }

 verifyCheckBox(event,checkbxval) {
  console.log("checkbxval="+checkbxval);
  this.linkval = checkbxval;
  if ( event.target.checked ) {
    this.verifycheckbx = true;
  } else {
    this.verifycheckbx = false;
    this.linkval = '';
  }
 }

 fn_redirect(agreement_no){
  console.log("agreement_no="+agreement_no);
  this.router.navigate(['/customerdetails',{'agreement_no':agreement_no}]);
 }



}
