import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http';
import { MyserviceService } from '../myservice.service';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';


@Component({
  selector: 'app-userdetails',
  templateUrl: './userdetails.component.html',
  styleUrls: ['./userdetails.component.css']
})
export class UserdetailsComponent implements OnInit {
	collection = [];
	UserID = '';
  constructor(private formBuilder: FormBuilder,private router: Router, private http: HttpClient,public myservice: MyserviceService,private route: ActivatedRoute) { }

  ngOnInit() {
	  this.myservice.setTitle('User Details');
    this.UserID = this.route.snapshot.paramMap.get('userid'); 
    console.log("UserID="+this.UserID);
	  
	let params = {'recidval':this.UserID};
       
	this.myservice.httpPostMethod('http://localhost:81/Angular/getuserdetails.php', params, 'application/json', false, 'userdetails')
	.then(resData => {

	if(resData != undefined){
		console.log("inside of resdata");
		if(resData.hasOwnProperty('status')){
			console.log("inside of hasOwnProperty");
			if(resData['status'] == 200){
			  console.log("status=",resData);
			  let emirates = resData['body'];
			  console.log("emirates="+emirates);//return false;
				if(emirates != undefined && emirates != null && emirates != ""){
					console.log("&&&&&&",emirates.success);
					if(emirates.success) {
						var mesg = emirates.message;
						console.log("mesg="+mesg);
						var splitcol = mesg.split('$$');
						       
						this.collection.push({ "username": splitcol[0], "lastname": splitcol[1], "firstname": splitcol[2],  "company": splitcol[3],  "mobileno": splitcol[4],  "reportsto": splitcol[5],  "grpname": splitcol[6],  "usertype": splitcol[7],  "product": splitcol[8],  "emailid": splitcol[9],  "department": splitcol[11],  "role": splitcol[10]});
					} 
				}
			}else{ 
			  console.log("Get City List API - Error Occured : "+resData['txt']);
			  return false;
			}
		}
	}
	  },
	  error =>  {
		console.log("Error", error);
		//this.spinner.hide();
	  });
  }

	GoBack(){
		this.router.navigate(['/userslist']);
	}

}

