import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { HeaderComponent } from '../header/header.component';

import { MyserviceService } from '../myservice.service';

@Component({
  selector: 'app-customerdetails',
  templateUrl: './customerdetails.component.html',
  styleUrls: ['./customerdetails.component.css']
})
export class CustomerdetailsComponent implements OnInit {
  //[x: string]: any;
  customerdetials : FormGroup;
  collection = [];
  recid:any ;
  constructor(private router: Router, private http: HttpClient,public myservice: MyserviceService,private route: ActivatedRoute,private formBuilder: FormBuilder,) { }

  ngOnInit() {
    this.myservice.setTitle('Customer Details');

    this.recid = this.route.snapshot.paramMap.get('agreement_no'); 
    console.log("recid="+this.recid);
    this.customerdetials = this.formBuilder.group({
      ge_chkbx: ['', ''],
      sr_chkbx: ['', ''],
      cr_chkbx: ['', ''],
    });

    let params = {'agreement_no':this.recid};
       
    this.myservice.httpPostMethod('http://localhost:81/Angular/get_details.php', params, 'application/json', false, 'custdetails')
    .then(resData => {

      if(resData != undefined){
        console.log("inside of resdata");
        if(resData.hasOwnProperty('status')){
          console.log("inside of hasOwnProperty");
          if(resData['status'] == 200){
            console.log("status=",resData);
            let emirates = resData['body'];
            console.log("emirates="+emirates);//return false;
            if(emirates != undefined && emirates != null && emirates != ""){
              console.log("&&&&&&",emirates.success);
              if(emirates.success) {
                var mesg = emirates.message;
                console.log("mesg="+mesg);
                var splitrw = mesg.split('||');
                console.log("splitrw="+splitrw.length);
                var reclen = splitrw.length;
                for(var i=0;i<reclen-1;i++) {
                  console.log("splitrw of i="+splitrw[i]);
                    var splitcol = splitrw[i].split('$$');
                    console.log("splitcol="+splitcol.length);
                    this.collection.push({ "name": splitcol[0], "dob": splitcol[1], "voter_id": splitcol[2], "gst_no": splitcol[3], "email_id": splitcol[4], "pan_no": splitcol[5], "father_name": splitcol[6], "aadhaar_no": splitcol[7], "driving_license_no": splitcol[8], "passport_no": splitcol[9],"cin_no": splitcol[10], "alternate_mobile_no": splitcol[11], "co_applicant_name": splitcol[12], "guarantor_name": splitcol[13], "address_type": splitcol[14], "stay_duration": splitcol[15], "property": splitcol[16], "address": splitcol[17], "city": splitcol[18], "state": splitcol[19], "district": splitcol[20], "country": splitcol[21], "pin_code": splitcol[22], "phone_1": splitcol[23], "phone_2": splitcol[24], "mobile_number": splitcol[25], "agreementid": splitcol[26], "customer_id": splitcol[27], "cif_no": splitcol[28], "group_id": splitcol[29], "loan_accno": splitcol[30], "disbursal_date": splitcol[31], "mode_of_repayment": splitcol[32], "branch_name": splitcol[33], "bank": splitcol[34], "spdc_chq_no": splitcol[35], "upi_address": splitcol[36], "unbanked_pdc": splitcol[37], "bank_acc_type": splitcol[38], "bank_acc_no": splitcol[39], "source_type": splitcol[40], "loan_agreementno": splitcol[41], "amount_financed": splitcol[42], "vehicle_number": splitcol[43], "loan_tenure": splitcol[44], "total_over_due": splitcol[45], "loan_status": splitcol[46], "api_type": splitcol[47] });
                }
              } else {
                alert("Invalid User credentilas");
                return false;
              }
            }
          }else{ 
            console.log("Get City List API - Error Occured : "+resData['txt']);
            return false;
          }
        }
      }
    },
    error =>  {
      console.log("Error", error);
      //this.spinner.hide();
    });

  }

  CreateRequest(requestvalue) {
    console.log("CreateRequest function="+requestvalue.ge_chkbx);
    let chkbxval;
    if(requestvalue.sr_chkbx == true) {
     chkbxval = 'SR';
    } else if(requestvalue.cr_chkbx == true) {
      chkbxval = 'CR';
    } else {
      chkbxval = 'GE';
    }
    if(requestvalue.ge_chkbx!='' && requestvalue.sr_chkbx!='' && requestvalue.cr_chkbx!='') {
      alert("Please Select Any CheckBox");
      return false;
    } else {
      this.router.navigate(['/custrequest',{'request_type':chkbxval,'agreement_no':this.recid}]);
    }
    
  }

}
